import React,{useState} from 'react'
import './Navbar.css'
import Button from './Button'
import Dropdown from './Dropdown'
import DropAbout from './DropAbout'
import {Link} from 'react-router-dom'

const Navbar = () => {
    const [click,setClick] = useState(false);
    const [dropdown, setDropdown] = useState(false);

    const handleClick =()=>{
        setClick(!click);
    }
    const closeMenu =() => {setClick(false)}

    const onMouseEnter =() =>{
        if(window.innerWidth < 960){
            setDropdown(false)
        }else{
            setDropdown(true);
        }
    }

    const onMouseLeave =() =>{
        if(window.innerWidth < 960){
            setDropdown(false)
        }else{
            setDropdown(false);
        }
    }
    return (
        <>
            <nav className="navbar">
                <Link to="/" className="navbar-logo">ACE|QUANTS</Link>
                <div className="menu-icon" onClick={handleClick}>
                    <i className={click ? 'fa fa-times' : 'fa fa-bars'}></i>
                </div>
                <ul className={click ? 'nav-menu active' : 'nav-menu'}>

                    <li className='nav-item'>
                        <Link to='/' className="nav-links" 
                        onClick={closeMenu}>Home</Link>
                    </li>

                    <li className='nav-item' 
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}>
                        <Link to='/services' className="nav-links" 
                        onClick={closeMenu}>Services <i className="fa fa-caret-down"/></Link>
                        {dropdown && <Dropdown/>}
                    </li>
                    <li className='nav-item'
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}>
                        <Link to='/about' className="nav-links" 
                        onClick={closeMenu}>About <i className="fa fa-caret-down"/></Link>
                        {dropdown && <DropAbout/>}
                    </li>
                    <li className='nav-item'>
                        <Link to='/company' className="nav-links" 
                        onClick={closeMenu}>Company <i className="fa fa-caret-down"/></Link>
                        {dropdown && <Dropdown/>}
                    </li>
                    <li className='nav-item'>
                        <Link to='/media' className="nav-links" 
                        onClick={closeMenu}>media <i className="fa fa-caret-down"/></Link>
                        {dropdown && <Dropdown/>}
                    </li>
                    <li className='nav-item'>
                        <Link to='/contacts' className="nav-links" 
                        onClick={closeMenu}>contacts</Link>
                    </li>
                    <span className='nav-item'>
                        <Link to='/startProject' className="nav-links-mobile" 
                        onClick={closeMenu}>Start a project</Link>
                    </span>
                </ul>
                <Button/>
            </nav>
        </>
    )
}

export default Navbar
